// This directive tells Next.js that this component runs in the browser (client-side)
// rather than on the server
'use client';
import React, { useEffect, useRef, useState } from 'react';

const GOOGLE_MAPS_API_KEY = process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY || '';

export default function GoogleMapsComponent() {
  const mapRef = useRef(null);
  const mapInstanceRef = useRef(null);
  const [isLoaded, setIsLoaded] = useState(false);

  // Load Google Maps
  useEffect(() => {
    if ((window as any).google?.maps) {
      setIsLoaded(true);
      return;
    }

    const script = document.createElement('script');
    script.src = `https://maps.googleapis.com/maps/api/js?key=${GOOGLE_MAPS_API_KEY}&v=weekly&libraries=marker`;
    script.async = true;
    script.onload = () => setIsLoaded(true);
    document.head.appendChild(script);

    return () => {
      if (script.parentNode) {
        document.head.removeChild(script);
      }
    };
  }, []);

  // Initialize map
  useEffect(() => {
    if (!isLoaded || !mapRef.current || mapInstanceRef.current) return;

    const google = (window as any).google;
    const map = new google.maps.Map(mapRef.current, {
      zoom: 15,
      center: { lat: 40.7200, lng: -73.8440 },
      mapTypeControl: true,
      streetViewControl: true,
      fullscreenControl: true,
    });

    mapInstanceRef.current = map;
  }, [isLoaded]);

  return (
    <div className="w-full h-screen flex flex-col">
      {!isLoaded && (
        <div className="flex items-center justify-center h-full bg-gray-100">
          Loading Map...
        </div>
      )}
      <div ref={mapRef} className="w-full h-full" />
    </div>
  );
}